import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';

const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json',
    })
  };

  

@Injectable()



export class JsonCreate {


constructor(private http: HttpClient) { }
date = new Date().toLocaleDateString()
configUrl = 'https://jsonbox.io/box_ce820a7b8b65281a8753';
configCollections ='https://jsonbox.io/box_ce820a7b8b65281a8753?q=collection:'
configChecklist = "https://jsonbox.io/box_26ac51c939fbfa8ae80d"


configUrlJulia = "https://jsonbox.io/box_308c1794496d93cb21b4"
configCollectionsJulia ="https://jsonbox.io/box_308c1794496d93cb21b4?q=collection:"
configCheckListJulia= "https://jsonbox.io/box_5bbd8261b8ebb3688bb9"

configUrlIlya = "https://jsonbox.io/box_c69057ba38a03c1aeab1"
configCollectionsIlya="https://jsonbox.io/box_c69057ba38a03c1aeab1?q=collection:"
configChecklistIlya="https://jsonbox.io/box_b0b06100f9bd43fa7abb"


configDeleteAll = "https://jsonbox.io/box_f9ba3926dade96693495?q=message:**"//удалить все
configDelete = 'https://jsonbox.io/box_ce820a7b8b65281a8753/'

configDeleteIlya = "https://jsonbox.io/box_c69057ba38a03c1aeab1/"
getConfig() {
  return this.http.get(this.configUrlIlya);
}

getSelectedConfig(selectedConfig) {
  console.log(selectedConfig)
  if(selectedConfig === "all"){
    return this.http.get(this.configUrlIlya)
  }
  else return this.http.get(this.configCollectionsIlya+selectedConfig)
}

postJson(title,message,collection) {
    const data ={
      title,
      message,
      collection

    }
    console.log(data)
    return this.http.post(this.configUrlIlya, data, httpOptions)
}

postCheckList(jsonArray){
  return this.http.post(this.configChecklistIlya, jsonArray, httpOptions)
}

getSelectedCheckList() {
   return this.http.get(this.configChecklistIlya +"?q=date:"+this.date)
}

deleteData() {
  return this.http.delete(this.configChecklist +"?q=date:**")
}
deleteSelectedElement(id: string) {
  console.log(this.configDeleteIlya+id)
  return this.http.delete(this.configDeleteIlya+id)
}


}

